// 格式化时间的插件
import moment from "moment"

// 超过字数变成省略号
let slh = function (value, wordLimit) {
    // var str = $(textName).html()
    // console.log(str)
    // var subStr = str.substring(0, wordLimit)
    // // console.log(subStr)
    // $(textName).html(subStr + (str.length > wordLimit ? "..." : ""))
    let subStr = value.substring(0, wordLimit)
    let newSubStr = subStr + (value.length > wordLimit ? "..." : "")
    return newSubStr
};

// 格式化时间
let getTime = function (dataStr, pattern = "YYYY-MM-DD HH:mm:ss") {
    return moment(dataStr).format(pattern);
}

// 文字去空格
let wordTrim = function(val){
    return val.trim()
}

// 加上随机数字
let addNum = function (val, min, max) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min + val;
}


export default {
    slh,
    getTime,
    addNum,
    wordTrim
}
