                // 滚动事件为window，非vue对象，存储this
                let _this = this;
                // console.log(this.$refs.search)
                let webScroll = document.body.scrollTop || document.documentElement.scrollTop;
                if (webScroll >= 200) {
                    // console.log("ok")
                    this.$refs.search.classList.add("search-fix");
                } else {
                    this.$refs.search.classList.remove("search-fix");
                }
            },
        },
        mounted() {
            // this.topBarFixed()
            window.addEventListener('scroll', this.topBarFixed, true)
